<template>
  <div v-if="model" class="editwrite">
    <div style="margin-bottom:10px;">
      <nav-bar></nav-bar>
    </div>

    <div class="uploadfile">
      <van-uploader :after-read="afterRead" preview-size="100vw" class="uploadimg" />
      <edit-info left="头像">
        <img :src="model.user_img" alt slot="right" v-if="model.user_img" />
        <img src="../assets/img/5.jpg" alt slot="right" v-else />
      </edit-info>
    </div>
    <edit-info left="昵称" @writeclick="show=true">
      <a href="javascript:;" slot="right">{{model.name}}</a>
    </edit-info>
    <edit-info left="账号">
      <a href="javascript:;" slot="right">{{model.username}}</a>
    </edit-info>
    <edit-info left="性别" @writeclick="gendershow=true">
       <a href="javascript:;" slot="right">{{model.gender==1?'男':'女'}}</a>
    </edit-info>
    <!-- <edit-info left="出生日期"></edit-info> -->
    <edit-info left="个性签名" @writeclick="textshow=true">
       <a href="javascript:;" slot="right">{{model.user_desc}}</a>
       </edit-info>
<div class="editback" @click="$router.back()">返回个人中心</div>
    <van-dialog v-model="show" title="昵称" show-cancel-button @confirm="confirm" @cancel="content=''">
  <van-field v-model="content"   autofocus/>
</van-dialog>
<van-dialog v-model="textshow" title="个性签名" show-cancel-button @confirm="textclick" @cancel="content=''">
  <van-field v-model="content" type="textarea"  autofocus/>
</van-dialog>

<!-- 性别展示 -->
<van-action-sheet v-model="gendershow" :actions="actions" @select="onSelect"  cancel-text="取消" />
  </div>
</template>

<script>
import NavBar from "../components/common/NavBar";
import EditInfo from "../components/common/editinfo";

export default {
  data() {
    return {
      model: {},
      show:false,
      textshow:false,
      gendershow:false,
      content:'',
      actions:[
        {name:'男',val:1},
        {name:'女',val:0}
      ]
    };
  },
  components: {
    NavBar,
    EditInfo,
    
  },
  mounted() {
    document
      .querySelector("body")
      .setAttribute("style", "background-color:#f7f7f7");
  },
  methods: {
    async editdata() {
      const res = await this.$http.get("/user/" + localStorage.getItem("id"));
      this.model = res.data[0];
    },
   async afterRead(file){
const fromdata = new FormData()
fromdata.append('file',file.file)
const res=await this.$http.post('/upload/',fromdata)
this.model.user_img=res.data.url
this.userupdata()
    },
   async userupdata(){
     const res= await this.$http.post('/update/' + localStorage.getItem('id'),this.model)
     if(res.data.code==200){
       this.$msg.fail('修改成功')
     }
        
    },
    // 昵称
    confirm(){
this.model.name=this.content
this.userupdata()
this.content=''
    },
    // 个性请签名
    textclick(){
      this.model.user_desc=this.content
      this.userupdata()
      this.content=''
    },
    // 性别
onSelect(data){
this.model.gender=data.val
this.userupdata()
this.gendershow=false
}
  },
  created() {
    this.editdata();
  }
};
</script>

<style scoped lang="less">
.editwrite img {
  height: 12.778vw;
  width: 12.778vw;
  border-radius: 50%;
  border: 1px solid green;
}
.uploadfile{
    position: relative;
    overflow: hidden;
    .uploadimg{
position: absolute;
opacity: 0;
    }
}
.editback{
  margin-top: 5.556vw;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #999;
  padding:4.167vw 0;
  font-size: 3.333vw;
}
</style>